import React, { useEffect, useState } from "react";

import OpenCard from "./OpenCard";
import PoolCard from "./PoolCard";

// import { activePools } from "../../assets/variables";
import { pools } from "../../assets/variables";
import leftsidebigmamba from "../../assets/img/leftside_Big_mamba.svg";

const ProjectsOpen = () => { 

    return (
      <>
        <section className="container projects-open">
          <h3
            className="col-12 text-center font-weight-bold sub-title"
            style={{ marginTop: 70 }}
          >
            ACTIVE POOLS
          </h3>
          {/* <section className="container open-cards">
            {activePools.map((project, index) => {
              return (
                project.status === "Open" && (
                  <OpenCard key={index} project={project} />
                )
              );
            })}
          </section> */}
          <img
            src={leftsidebigmamba}
            alt="leftsidebigmamba"
            className="activepool-mamba"
          />
          <section className="container pool-cards">
            {pools.map((pool, index) => {
              return <PoolCard key={index} pool={pool} />;
            })}
          </section>
        </section>
        <hr style={{ border: "1px solid #fdac42" }} />
      </>
    );
}


export default ProjectsOpen;