import React, { useState, useEffect } from "react";
import { Row, Col, Table, Form } from "react-bootstrap";

import {
  SiWebpack,
  AiFillTwitterCircle,
  AiOutlineMedium,
  FaTelegramPlane,
} from "react-icons/all";
import Countdown from "react-countdown";
import SocialLink from "../../components/SocialLink";

import PropTypes from "prop-types";

export default function PoolCard({ pool }) {
  const [amount, setAmount] = useState(0);
  const [contribution, setContribution] = useState(0);
  const [endTime, setEndTime] = useState(Date.now() + 70000);

  const handleAmount = (e) => {
    setAmount(e.target.value);
    setContribution((e.target.value * 100000) / pool.weight);
  };
  const handleContribution = (e) => {
    setContribution(e.target.value);
    setAmount((e.target.value / 100000) * pool.weight);
  };

  useEffect(() => {
    const savedDateRaw = localStorage.getItem("end_date");
    const savedDate = savedDateRaw * 1.0;
    // console.log(localStorage.getItem("end_date")*1.0, Date.now(), Boolean(localStorage.getItem('end_date')<Date.now()))
    setEndTime(savedDate);
    if (savedDate === null || savedDate === Date.now() || savedDate === 0) {
      const date = Date.now() + 60000;
      // console.info('date', date);
      // console.info(typeof date);
      setEndTime(date);
      localStorage.setItem("end_date", date);
    }
  }, [endTime]);

  return (
    <section className="pool-card">
      <div className="pool-card-header">
        <a href="/">
          <img src={pool.picture} alt="pool profile"></img>
        </a>
        <div className="pool-card-title">
          <strong>{pool.name}</strong>
          <div className="social-links">
            <a href="https://www.google.com">
              <SiWebpack className="social-link" />
            </a>
            <a href="https://www.twitter.com">
              <AiFillTwitterCircle className="social-link" />
            </a>
            <a href="https://www.medium.com">
              <AiOutlineMedium className="social-link" />
            </a>
            <a href="https://www.telegram.com">
              <FaTelegramPlane className="social-link" />
            </a>
          </div>
        </div>
        <p style={{ color: "orange" }}>{pool.tier_name}</p>
      </div>
      <div className="pool-card-body">
        <section>
          <h4 style={{ marginBottom: 10, textAlign: "center" }}>
            Total Amount per IDO: {pool.total_amount} BNB{" "}
          </h4>
          <h4 style={{ marginBottom: 10, textAlign: "center" }}>
            Pool OPEN: {pool.open_time}
          </h4>
          <h4 style={{ marginBottom: 0, textAlign: "center" }}>
            Pool will be open for {pool.duration} hours
          </h4>
          <Row style={{ marginTop: "20px" }}>
            <Col lg={7} style={{ marginTop: "10px" }}>
              <Table className="allocation-table" bordered hover variant="dark">
                <thead>
                  <tr>
                    <th colspan="2">ALLOCATION(BNB)</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>MIN</td>
                    <td>{pool.min_allocation}</td>
                  </tr>
                  <tr>
                    <td>MAX</td>
                    <td>{pool.max_allocation}</td>
                  </tr>
                </tbody>
                <thead>
                  <tr>
                    <th colspan="2">PRICE(USDT)</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>IDO</td>
                    <td>{pool.ido_price}</td>
                  </tr>
                  <tr>
                    <td>LISTING</td>
                    <td>{pool.listing_price}</td>
                  </tr>
                </tbody>
              </Table>
            </Col>
            <Col lg={5} style={{ padding: "0 30px" }}>
              <h3 style={{ marginBottom: 0, fontSize: "14px" }}>AMOUNT(BNB)</h3>
              <Form.Control
                style={{ marginBottom: "20px" }}
                value={amount}
                onChange={handleAmount}
              />
              <div className="join-button">
                <button class="poolcard-button">JOIN POOL</button>
              </div>
              <h3
                style={{ marginBottom: 0, marginTop: "70px", fontSize: "14px" }}
              >
                CONTRIBUTION(MAMP)
              </h3>
              <Form.Control
                value={contribution}
                onChange={handleContribution}
              />
            </Col>
          </Row>
          <Row style={{ marginTop: "30px" }}>
            <Col lg={7}>
              <h3 style={{ marginBottom: 0, fontSize: "14px" }}>COUNTDOWN</h3>
              <div style={{ fontSize: "2em" }}>
                <Countdown
                  key={endTime.toString()}
                  date={localStorage.getItem("end_date")?localStorage.getItem("end_date")*1.0:endTime}
                  // renderer={renderer}
                  autoStart={()=>{
                    // if(localStorage.getItem("end_date")*1.0<Date.now())
                      localStorage.removeItem("end_date");
                  }}
                  onComplete={()=>{
                    if(localStorage.getItem("end_date")*1.0<Date.now())
                      localStorage.removeItem("end_date");
                  }}
                  daysInHours
                />
              </div>
            </Col>
            <Col lg={5} style={{ padding: "0 30px" }}>
              <h3 style={{ marginBottom: 0, fontSize: "14px" }}>
                MAMP TOKEN
              </h3>
              <button class="poolcard-button">CLAIM</button>
            </Col>
          </Row>
        </section>
      </div>
    </section>
  );
}

PoolCard.propTypes = {
  pool: PropTypes.object.isRequired,
};
